import React from "react"
import { graphql, useStaticQuery } from "gatsby"
import Img from "gatsby-image"
import "../css/style.css"
import bann02 from "../images/bann2.png"

export default function Vision() {
    const data = useStaticQuery(graphql`
query Vision {
      span: file(relativePath: {eq: "span.png"}) {
        childImageSharp {
          fluid {
              src
              srcWebp
              aspectRatio
              base64
          }
        }
      }
  }`
)

    return (
        <section className="mision-area">
            <div className="div-tittle-mision">
                <Img className="span" alt="" fluid={data.span.childImageSharp.fluid} />
                <p>Visión</p>
                <div className="div-text-mision">
                    <p> Aspira a ser referencia en una limpieza sostenible respetuosa con el medio ambiente y las personas que lo habitan, así como brindar la mejor experiencia de limpieza 
                        a nuestros clientes.</p>
                </div>
            </div>
            <img className="mision-banner" alt="" src={bann02} />
        </section>
    )
}