import React from "react"
import { graphql, useStaticQuery } from "gatsby"
import Img from "gatsby-image"
import "../css/style.css"
import bann01 from "../images/bann1.png"

export default function Mision() {
    const data = useStaticQuery(graphql`
query Mision {
      span: file(relativePath: {eq: "span.png"}) {
        childImageSharp {
          fluid {
              src
              srcWebp
              aspectRatio
              base64
          }
        }
      }
  }`
)

    return (
        <section className="mision-area">
            <div className="div-tittle-mision">
                <Img className="span" alt="" fluid={data.span.childImageSharp.fluid} />
                <p>Misión</p>
                <div className="div-text-mision">
                    <p>Facilitar a nuestros clientes un entorno limpio, saludable y respetuoso con las personas que habitan en su establecimiento proporcionando una limpieza profesional 
                       sostenible, una limpieza con unos servicios rápidos, homogéneos, de confianza y a precios que se adapten a sus necesidades, anticipando nuestras acciones a posibles 
                       incidencias y utilizando las técnicas, materiales y equipos sostenibles más modernos para cumplir las más exigentes expectativas de nuestros Clientes.</p>
                </div>
            </div>
            <img className="mision-banner" alt="" src={bann01} />
        </section>
    )
}