import React from "react"
import { graphql, useStaticQuery } from "gatsby"
import Img from "gatsby-image"
import "../css/style.css"
import bann02 from "../images/bann2.png"

export default function Listservices() {
    const data = useStaticQuery(graphql`
query Listservices {
      span: file(relativePath: {eq: "span.png"}) {
        childImageSharp {
          fluid {
              src
              srcWebp
              aspectRatio
              base64
          }
        }
      }
  }`
)

    return (
        <section className="listservicios-area">
            <div className="div-tittle-mision">
                <Img className="span" alt="" fluid={data.span.childImageSharp.fluid} />
                <div className="grid-container-servicios">
                <p>¿Qué incluye el servicio de limpieza?</p>
                <div className="div-text-servicios">
                    <p className="flecha">&nbsp;&nbsp;&nbsp;Personal capacitado</p>
                    <p className="flecha">&nbsp;&nbsp;&nbsp;Imagen corporativa (uniformes)</p>
                    <p className="flecha">&nbsp;&nbsp;&nbsp;Químicos biodegradables</p>
                    <p className="flecha">&nbsp;&nbsp;&nbsp;Jarciería (material de limpieza)</p>
                    <p className="flecha">&nbsp;&nbsp;&nbsp;Supervisión</p>
                    <p className="flecha">&nbsp;&nbsp;&nbsp;Monitoreo y control</p>
                    <p className="flecha">&nbsp;&nbsp;&nbsp;Calidad</p>
                </div>
                </div>
            </div>
        </section>
    )
}