import React from "react"
import { graphql, useStaticQuery } from "gatsby"
import Img from "gatsby-image"
import "../css/style.css"
import bann01 from "../images/bann1.png"

export default function Listvalores() {
    const data = useStaticQuery(graphql`
query Listvalores {
      span: file(relativePath: {eq: "span.png"}) {
        childImageSharp {
          fluid {
              src
              srcWebp
              aspectRatio
              base64
          }
        }
      }
      uno: file(relativePath: {eq: "uno.png"}) {
        childImageSharp {
          fluid {
              src
              srcWebp
              aspectRatio
              base64
          }
        }
      }
      dos: file(relativePath: {eq: "dos.png"}) {
        childImageSharp {
          fluid {
              src
              srcWebp
              aspectRatio
              base64
          }
        }
      }
      tres: file(relativePath: {eq: "tres.png"}) {
        childImageSharp {
          fluid {
              src
              srcWebp
              aspectRatio
              base64
          }
        }
      }
      cuatro: file(relativePath: {eq: "cuatro.png"}) {
        childImageSharp {
          fluid {
              src
              srcWebp
              aspectRatio
              base64
          }
        }
      }
      cinco: file(relativePath: {eq: "cinco.png"}) {
        childImageSharp {
          fluid {
              src
              srcWebp
              aspectRatio
              base64
          }
        }
      }
      seis: file(relativePath: {eq: "seis.png"}) {
        childImageSharp {
          fluid {
              src
              srcWebp
              aspectRatio
              base64
          }
        }
      }
      siete: file(relativePath: {eq: "siete.png"}) {
        childImageSharp {
          fluid {
              src
              srcWebp
              aspectRatio
              base64
          }
        }
      }
      ocho: file(relativePath: {eq: "ocho.png"}) {
        childImageSharp {
          fluid {
              src
              srcWebp
              aspectRatio
              base64
          }
        }
      }
  }`
)

    return (
        <section className="listvalores-area">
            <div className="div-tittle-listvalores">
                <Img className="span" alt="" fluid={data.span.childImageSharp.fluid} />
                <p className="tittle-listvalores">Nuestros valores definen quiénes somos y están a su disposición para establecer objetivos comunes cómo lo son:</p>
                <div className="div-text-listvalores">
                    <div className="grid-container-valores">
                        <div className="grid-container-lista">
                        <Img className="label-listvalores" alt="" fluid={data.uno.childImageSharp.fluid} />
                        <p><b>Calidad:</b> Nos gusta estar siempre comprometidos con procesos de mejora continua en los cuales todas las áreas 
                            de la empresa participen activamente en el desarrollo de productos y servicios que puedan satisfacer las 
                            expectativas de nuestros Clientes</p>
                        </div>
                        <div className="grid-container-lista">
                        <Img className="label-listvalores" alt="" fluid={data.dos.childImageSharp.fluid} />
                        <p><b>Transparencia:</b> Somos respetuosas con las normas y leyes establecidas que implican 
                            procedimientos honrados y totalmente claros que garantizan la legalidad de los mismos.</p>
                        </div>
                        <div className="grid-container-lista">
                        <Img className="label-listvalores" alt="" fluid={data.tres.childImageSharp.fluid} />
                        <p><b>Honestidad:</b> Nos gusta y creemos en la honestidad como uno de los pilares fundamentales 
                            que rigen todas las actividades de cualquier empresa y nos comportamos y expresamos con 
                            coherencia y sinceridad de acuerdo con los valores de verdad y justicia.</p>
                        </div>
                        <div className="grid-container-lista">
                        <Img className="label-listvalores" alt="" fluid={data.cuatro.childImageSharp.fluid} />
                        <p><b>Compromiso:</b> Destacamos el valor del compromiso de cada integrante de la empresa 
                            con sus responsabilidades asumidas, creemos en el trabajo de cada uno y en su valor dentro de 
                            la empresa.</p>
                        </div>
                        <div className="grid-container-lista">
                        <Img className="label-listvalores" alt="" fluid={data.cinco.childImageSharp.fluid} />
                        <p><b>Innovación:</b> Nos encanta innovar, buscar siempre procesos constantes de desarrollo
                            y poder implementar nuevas ideas en productos o servicios que encajen con la estrategia 
                            de la empresa y aporten valor añadido a nuestros clientes.</p>
                        </div>
                        <div className="grid-container-lista">
                        <Img className="label-listvalores" alt="" fluid={data.seis.childImageSharp.fluid} />
                        <p><b>Rentabilidad:</b> Sabemos que la rentabilidad es una responsabilidad primordial en cualquier 
                            empresa, por lo que gestionamos de manera efectiva los recursos para obtener resultados 
                            económicos que beneficien a Star Clean y a todos sus miembros, garantizando la permanencia de
                            la misma.</p>
                        </div>
                        <div className="grid-container-lista">
                        <Img className="label-listvalores" alt="" fluid={data.siete.childImageSharp.fluid} />
                        <p><b>Respeto:</b> Nos encanta fomentar la capacidad de aceptar diferentes criterios y actitudes 
                            dentro de la filosofía de nuestra empresa.</p>
                        </div>
                        <div className="grid-container-lista">
                        <Img className="label-listvalores" alt="" fluid={data.ocho.childImageSharp.fluid} />
                        <p><b>Trabajo en equipo:</b> Sabemos que somos un grupo de personas con diferentes capacidades 
                            complementarias, comprometidas con un propósito y un objetivo de trabajo, guiadas por un 
                            planeamiento común con responsabilidades compartidas.</p>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}